<template>
  <div>
    <!-- Header 区域 -->
    <van-nav-bar fixed title="文章详情" left-arrow @click-left="$router.back()" />

    <!-- 文章等待加载中 -->
    <van-loading color="#0094ff" v-if="Object.keys(detailContent).length===0">
      文章拼命加载中...</van-loading>

    <!-- 文章内容区域 -->
    <div v-else>
      <!-- 文章信息区域 -->
      <div class="article-container">
        <!-- 文章标题 -->
        <h1 class="art-title">{{detailContent.title}}</h1>

        <!-- 用户信息 -->
        <van-cell center :title="detailContent.aut_name"
          :label="formatTime(detailContent.pubdate)">
          <template #icon>
            <img :src="detailContent.aut_photo" alt="" class="avatar" />
          </template>
          <template #default>
            <div>
              <van-button type="info" size="mini"
                v-if="detailContent.is_followed" @click="unFollow">已关注
              </van-button>
              <van-button icon="plus" type="info" size="mini" plain v-else
                @click="userFollow">关注
              </van-button>

            </div>
          </template>
        </van-cell>

        <!-- 分割线 -->
        <van-divider></van-divider>

        <!-- 文章内容 -->
        <div class="art-content" v-html="detailContent.content"></div>

        <!-- 分割线 -->
        <van-divider>End</van-divider>

        <!-- 点赞 -->
        <div class="like-box">
          <van-button icon="good-job" type="danger" size="small"
            v-if="detailContent.attitude===1" @click="articleLikeFn(true)">已点赞
          </van-button>
          <van-button icon="good-job-o" type="danger" plain size="small" v-else
            @click="articleLikeFn(false)">点赞
          </van-button>
        </div>
      </div>

      <!-- 文章评论 -->
      <div>
        <commentList></commentList>
      </div>

    </div>
  </div>
</template>

<script>
import { getArticleDetail, userFollowing, unUserFollow, articleLiking, UnArticleLike } from '@/api'
import { timeAgo } from '@/utils/date.js'
import commentList from './CommentList.vue'
export default {
  name: 'ArticleDetail',
  data () {
    return {
      artId: '',
      detailContent: {}
    }
  },
  components: {
    commentList
  },
  async created () {
    this.artId = this.$route.query.aid
    const res = await getArticleDetail(this.artId)
    // console.log(res)
    this.detailContent = res.data.data
  },
  methods: {
    formatTime: timeAgo,

    // 关注用户
    async userFollow () {
      const res = await userFollowing(this.detailContent.aut_id)
      console.log(res)
      this.detailContent.is_followed = true
    },
    // 取消关注
    async unFollow () {
      const res = await unUserFollow(this.detailContent.aut_id)
      console.log(res)
      this.detailContent.is_followed = false
    },
    // 文章点赞/取消点赞
    articleLikeFn (bool) {
      if (bool === true) {
        // 取消点赞
        // console.log('取消点赞')
        UnArticleLike(this.detailContent.art_id)
        this.detailContent.attitude = -1
      } else {
        // 点赞
        // console.log('点赞')
        articleLiking(this.detailContent.art_id)
        this.detailContent.attitude = 1
      }
    }
  }
}
</script>

<style scoped lang="less">
.article-container {
  padding: 10px;
  margin-top: 46px;
}
.art-title {
  font-size: 16px;
  font-weight: bold;
  margin: 10px 0;
}

.art-content {
  font-size: 12px;
  line-height: 24px;
  width: 100%;
  overflow-x: scroll;
  word-break: break-all;
  /deep/ img {
    width: 100%;
  }
  /deep/ pre {
    white-space: pre-wrap;
    word-wrap: break-word;
  }
}

.van-cell {
  padding: 5px 0;
  &::after {
    display: none;
  }
}

.avatar {
  width: 60px;
  height: 60px;
  border-radius: 50%;
  background-color: #f8f8f8;
  margin-right: 5px;
  border: none;
}

.like-box {
  display: flex;
  justify-content: center;
}

.van-loading {
  text-align: center;
  margin-top: 46px;
}
</style>
