<template>
  <div>

    <!-- 评论列表 -->
    <div class="cmt-list"
      :class="{'art-cmt-container-1':isShow,'art-cmt-container-2':!isShow}">
      <!-- 评论的 Item 项 -->
      <van-list v-model="loading" :finished="finished" finished-text="没有更多了"
        @load="onLoad" offset="500" :immediate-check="false">
        <div class="cmt-item" v-for="obj in commentsList" :key="obj.com_id">
          <!-- 头部区域 -->
          <div class="cmt-header">
            <!-- 头部左侧 -->
            <div class="cmt-header-left">
              <img :src="obj.aut_photo" alt="" class="avatar">
              <span class="uname">{{obj.aut_name}}</span>
            </div>
            <!-- 头部右侧 -->
            <div class="cmt-header-right">
              <van-icon name="like" size="16" color="red" v-if="obj.is_liking"
                @click="isLikingFn(true,obj)" />
              <van-icon name="like-o" size="16" color="gray" v-else
                @click="isLikingFn(false,obj)" />
            </div>
          </div>
          <!-- 主体区域 -->
          <div class="cmt-body">
            {{obj.content}}
          </div>
          <!-- 尾部区域 -->
          <div class="cmt-footer">{{formatTime(obj.pubdate)}}</div>
        </div>
      </van-list>
    </div>

    <!-- 底部添加评论区域 - 1 -->
    <div class="add-cmt-box van-hairline--top" v-if="isShow">
      <van-icon name="arrow-left" size="0.48rem" @click="$router.back()" />
      <div class="ipt-cmt-div" @click="isShowFn">发表评论</div>
      <div class="icon-box">
        <van-badge :content="totalCount||''" max="99">
          <van-icon name="comment-o" size="0.53333334rem" @click="swipeFn" />
        </van-badge>
        <van-icon name="star-o" size="0.53333334rem" />
        <van-icon name="share-o" size="0.53333334rem" />
      </div>
    </div>

    <!-- 底部添加评论区域 - 2 -->
    <div class="cmt-box van-hairline--top" v-else>
      <textarea placeholder="友善评论、理性发言、阳光心灵" v-gfocus
        v-model.trim="inputContent" @blur="blurFn"></textarea>
      <van-button type="default" :disabled="inputContent.length===0"
        @click="addCommentFn">发布
      </van-button>
    </div>
  </div>
</template>

<script>
import { getCommentsAPI, commentLikingAPI, unCommentLikingAPI, addCommentAPI } from '@/api'
import { timeAgo } from '@/utils/date.js'
export default {
  data () {
    return {
      commentsList: [],
      isShow: true,
      offset: null,
      totalCount: 0, // 评论总数量
      inputContent: '', // 发表输入框内容
      isdisabled: true, // 发布按钮状态
      loading: false,
      finished: false
    }
  },
  created () {
    this.updateCommentsList()
  },
  methods: {
    formatTime: timeAgo,
    isLikingFn (bool, obj) {
      if (bool === true) {
        // 用户点红心，取消红心，取消对评论的点赞
        obj.is_liking = false
        unCommentLikingAPI(obj.com_id)
      } else {
        // 用户点灰心，加红心，点赞评论
        obj.is_liking = true
        commentLikingAPI(obj.com_id)
      }
    },
    isShowFn () {
      this.isShow = false
    },
    blurFn () {
      setTimeout(() => {
        this.isShow = true
      })
    },
    // 点击图标 滚动到评论区
    // window.scrollTo(0, 滚动高度) 可以使网页进行滚动
    swipeFn () {
      // const articleHeight = document.querySelector('.article-container').scrollHeight // 需要滚动的高度
      // console.log(articleHeight)
      // window.scrollTo(0, articleHeight) //滚动僵硬
      document.querySelector('.like-box').scrollIntoView({
        behavior: 'smooth'
      })
    },
    // 发表评论
    async addCommentFn () {
      const res = await addCommentAPI({
        artId: this.$route.query.aid,
        content: this.inputContent
      })
      console.log(res)
      this.commentsList.unshift(res.data.data.new_obj)
      this.totalCount++
      this.inputContent = ''
      this.swipeFn()
      // this.updateCommentsList()
    },
    // 获取评论
    async updateCommentsList () {
      const res = await getCommentsAPI({
        artId: this.$route.query.aid,
        offset: this.offset
      })
      // console.log(res)
      this.commentsList = res.data.data.results
      this.totalCount = res.data.data.total_count
    },
    // 评论触底执行
    async onLoad () {
      if (this.commentsList.length === 0) {
        this.loading = false // 没有评论无需执行
        this.finished = true
        return
      }
      console.log('到底了')
      const res = await getCommentsAPI({
        artId: this.$route.query.aid,
        offset: this.commentsList[this.commentsList.length - 1].com_id// 本页文章评论最后一个id
      })
      console.log(res)
      if (res.data.data.results.length === 0) {
        this.finished = true
        return
      }
      this.commentsList = [...this.commentsList, ...res.data.data.results]
      this.loading = false
    }
    // 处理发布按钮方法2
    // watch: {
    //   inputContent () {
    //     if (this.inputContent.length > 0) {
    //       this.isdisabled = false // 发表输入框有数据，发布按钮可以点击
    //     } else {
    //       this.isdisabled = true
    //     }
    //   }
    // }
  }

}
</script>

<style scoped lang="less">
.cmt-list {
  padding: 10px;
  .cmt-item {
    padding: 15px 0;
    + .cmt-item {
      border-top: 1px solid #f8f8f8;
    }
    .cmt-header {
      display: flex;
      align-items: center;
      justify-content: space-between;
      .cmt-header-left {
        display: flex;
        align-items: center;
        .avatar {
          width: 40px;
          height: 40px;
          background-color: #f8f8f8;
          border-radius: 50%;
          margin-right: 15px;
        }
        .uname {
          font-size: 12px;
        }
      }
    }
    .cmt-body {
      font-size: 14px;
      line-height: 28px;
      text-indent: 2em;
      margin-top: 6px;
      word-break: break-all;
    }
    .cmt-footer {
      font-size: 12px;
      color: gray;
      margin-top: 10px;
    }
  }
}

/*美化 - 文章详情 - 底部的评论页面 */
// 外层容器
.art-cmt-container-1 {
  padding-bottom: 46px;
}
.art-cmt-container-2 {
  padding-bottom: 80px;
}

// 发布评论的盒子 - 1
.add-cmt-box {
  position: fixed;
  bottom: 0;
  left: 0;
  width: 100%;
  box-sizing: border-box;
  background-color: white;
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 46px;
  line-height: 46px;
  padding-left: 10px;
  .ipt-cmt-div {
    flex: 1;
    border: 1px solid #efefef;
    border-radius: 15px;
    height: 30px;
    font-size: 12px;
    line-height: 30px;
    padding-left: 15px;
    margin-left: 10px;
    background-color: #f8f8f8;
  }
  .icon-box {
    width: 40%;
    display: flex;
    justify-content: space-evenly;
    line-height: 0;
  }
}

.child {
  width: 20px;
  height: 20px;
  background: #f2f3f5;
}

// 发布评论的盒子 - 2
.cmt-box {
  position: fixed;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 80px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  font-size: 12px;
  padding-left: 10px;
  box-sizing: border-box;
  background-color: white;
  textarea {
    flex: 1;
    height: 66%;
    border: 1px solid #efefef;
    background-color: #f8f8f8;
    resize: none;
    border-radius: 6px;
    padding: 5px;
  }
  .van-button {
    height: 100%;
    border: none;
  }
}
</style>
